import $http from "./http.js"
//公共模块
const common = {
    // 查询 房间订单列表
    hotelInfo(obj) {
        return $http.get({
            url: "/mobile/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
}
//定房模块
const roomBooking = {
    // 查询 房间订单列表
    activitylist(obj) {
        return $http.get({
            url: "/mobile/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //首页房型列表
    roomCategory(obj) {
        return $http.get({
            url: "/mobile/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
}


export {
    common,
    roomBooking
}


